// npm
import Cropper from 'cropperjs';
import 'cropperjs/dist/cropper.css';
import CropperOverlay from "./CropperOverlay";
import CropperEventHandler from "./CropperEventHandler";
import CropperState from "./CropperState";

class CropperImage {
    constructor(image, changeBtn) {
        this.image = image;
        this.cropperInstance = null; // Cropper.js 
        this.changeBtn = changeBtn;
        this.choices = document.getElementsByName('choice')
        this.cropperState = null;
        
    }

    enableCropperEditing() {
        // cropper
        if (this.cropperInstance) {
            this.cropperInstance.enable();
        }
        // ()
        const cropperOverlay = new CropperOverlay()
        cropperOverlay.setVisibility(true)
    }

    //  Cropper 
    updateImage(newImage) {
        // 
        if (this.cropperInstance) {
            this.cropperInstance.destroy();
            this.cropperInstance = null;
        }

        // 
        this.image= newImage

        //  Cropper 
        this.choices.forEach((choice)=>{
            const newChoice = choice.cloneNode(true); // 
            choice.replaceWith(newChoice); // 


            newChoice.addEventListener("change", (e)=>{
                CropperEventHandler.handleUrlInputToggle(e)
                if(e.target.value === "on"){
                    this.resizeImage();
                }else{
                    if(this.cropperInstance){
                        this.destroyCropper()
                    }
                    
                }
            })
        })
    }

    resizeImage() {
        // Cropper.js 
        try{
            this.cropperInstance = new Cropper(this.image, {
                viewMode: 1, // 
                dragMode: "crop", // 
                autoCropArea: 1.0, // 
                responsive: true, // 
                restore: true, // 
                guides: true, // 
                highlight: true, // 
                cropBoxResizable: true, // 
                ready: () => {
                        // Cropper  crop 
                        const cropperArea = this.getCropperArea();
                },
                cropend: () => {
                    this.changeBtn.classList.remove("disabled_btn")
                    const cropBoxData = this.cropperInstance.getCropBoxData(); // 
                    const containerData = this.cropperInstance.getContainerData(); // 
                    const imageData = this.cropperInstance.getImageData(); // 

                    // %
                    this.cropperState = new CropperState(cropBoxData, imageData, containerData);
                    this.cropperState.updatePercentage()
                },
            }); 
        }catch(error){
            alert("")
        }
        
    }

    getCropperArea(){
        try{
                if(this.cropperInstance){
                    const cropBoxData = this.cropperInstance.getCropBoxData(); // 
                    const containerData = this.cropperInstance.getContainerData(); // 
                    const imageData = this.cropperInstance.getImageData(); // 
        
                    // %
                    this.cropperState = new CropperState(cropBoxData, imageData, containerData);
                    this.cropperState.updatePercentage()
                    return this.cropperState.getState()
                }

        }catch(error){
                console.log(error);
                
                alert("")
        }
    }


    // enableCropper(){
    //     this.cropperInstance = new CropperJS(this.image, {
    //         viewMode: 0.5, // 
    //         dragMode: "crop", // 
    //         autoCropArea: 0.5, // 
    //         responsive: true, // 
    //         restore: true, // 
    //         guides: true, // 
    //         highlight: true, // 
    //         cropBoxResizable: true, // 
    //     });
    // }

    disableCropper(){
        // cropper
        // 
        const cropperOverlay = new CropperOverlay()
        cropperOverlay.setVisibility(false)
        this.cropperInstance.disable();
    }

    destroyCropper(){
        
        this.cropperInstance.destroy();
        this.cropperInstance = null
    }
    getCropperState() {
        // CropperState 
        return this.cropperState;
    }

}

export default CropperImage;
