
const DELAY_TIME = 500
const MAX_LENGTH = 30

/**
 * ChatUIHelper
 * UI
 */
class ChatUIHelper{
    /**
     * 
     */
    static scrollToBottom(){
        setTimeout(() => {
            const scroll_el = document.querySelector(".chat__message-main");
            if (scroll_el) {
                scroll_el.scrollTo({
                    top: scroll_el.scrollHeight,
                    behavior: 'auto'
                });
            }
        }, DELAY_TIME);
    };



    /**
     * 
     *  "..." 
     * @param {String} text - 
     * @returns {String} -
     */
    static adjustMesageLength(text){
        const formattedText = text.split('\n')[0];
        if (formattedText.length >= MAX_LENGTH) {
            return formattedText.substring(0, MAX_LENGTH) + "...";
        }else{
            return formattedText
        }
    };

    /**
     * UUID
     * @param {string} sender_uuid - UUID(00bd10de-18dc-4d00-be56-02e5d12b03b2)
     * @returns {boolean} truefalse
     */
    static isCurrentUser(sender_uuid) {
        const user_uuid = document.getElementById("js_chatuser_id")?.value || null;
        return user_uuid === sender_uuid;
    }

    static isCurrentAmdin(sender_uuid, receiver_uuid){
        const admin_uuid = document.getElementById("js_uuid")?.value || null;
        const user_uuid = document.getElementById("js_chatuser_id")?.value || null
        return (admin_uuid === sender_uuid && user_uuid === receiver_uuid);
    }

    static updateUserInfo(userUuid, src, name){

        document.getElementById("js_receiver_id").value = userUuid
        document.querySelector(".js_append_admin").dataset.id = userUuid
        document.getElementById("js_chatuser_id").value = userUuid
        document.querySelector(".js_user_icon").src = src
        document.querySelectorAll(".chat_users-icon-message").forEach((icon)=> icon.src = src)
        document.querySelector(".chat_message_name").innerHTML = name
    }
}

export default ChatUIHelper;